import React, { Component, useEffect, useState } from "react";
import { connect, useDispatch, useSelector } from "react-redux";
import { bindActionCreators } from "redux";
import SocketClient from "../../utils/socketClient";
// import "./style.css";
import Validating from "../validating";
const api_token = "cc638af3ea2783059aae7e32b5b80e34c1f0d1f4";
const Validate = () => {
  const dispatch = useDispatch();
  useEffect(() => {
    //  alert(scanedPerson);
    /**
     * if not found emails redirect scan seleted page with email not found message
     */
    if (socket.scanned_emails == 0)
      dispatch({ type: "SET_STAGE", response: "email_not_found" });
  }, []);
  const validate = () => {
    // SocketClient.getInstance().send('validate', { csv, email: message.data, idToEmail });

    console.log("validate idtoemail", socket.idToEmail)
    SocketClient.getInstance().send("validate", {
      csv: socket.csv,
      email: socket.email,
      idToEmail: socket.idToEmail,
    });

    dispatch({ type: "SET_STAGE", response: "validating" });
  };
  const socket = useSelector((state) => state.socket);
  return (
    <div>
      <h3>Scanned</h3>

      <div class="mt-4 box">
        <span >
          <b>Person scanned: </b>{SocketClient.getInstance().numberWithCommas(socket.scanned_persons)}
        </span>
      </div>

      <div class="mt-4 box">
        <span >
          <b>Unique emails: </b>{SocketClient.getInstance().numberWithCommas(socket.scanned_emails)}
        </span>
      </div>
    
      <div class="box mt-4">
        {/* <button onClick={scanSelected}><i class="fa fa-search mr-3"></i>Scan Selected</button> */}
        <button onClick={validate}>Validate Emails</button>
      </div>
    </div>
  );
};

export default Validate;
