import React, { Component, useEffect, useState } from "react";
import { connect, useDispatch, useSelector } from "react-redux";

import SocketClient from "../../utils/socketClient";
import Credit from "../credit";
import Scan from "../scan";
import "./style.css";
import Loader from "react-loader-spinner";

const Config = require("../../utils/config");
const api_token = "cc638af3ea2783059aae7e32b5b80e34c1f0d1f4";

const Home = ({ props }) => {
  const [loginattempt, setLoginattempt] = useState(false);
  const socket = useSelector((state) => state.socket);

  const dispatch = useDispatch();
  var isLoggingIn = document.location.href.indexOf("code=") > -1;
  useEffect(() => {
    // SocketClient.getInstance().send("status", { token: api_token });
    if (!socket.loggedIn && isLoggingIn) {
      if (document.location.href.includes("code=")) {
        var params = document.location.href.split("code=")[1].split("#")[0];

        // if url has code, display the dot loader
        setLoginattempt(true);
        dispatch({ type: "OAUTH_CODE", response: params });
        (async () => {
          await SocketClient.getInstance().send("oauth", {
            params: params,
            tabId: "",
          });
          console.log("send status after oauth")
          await SocketClient.getInstance().send("status", { token: api_token, id:socket.user.id });
          console.log("status afcode", params);
          await SocketClient.getInstance().send("status", { token: api_token, id:socket.user.id });
        })();
      }
    }
    else if(socket.loggedIn){
      // alert()
      SocketClient.getInstance().send("status", { token: api_token, id:socket.user.id });
    }
    // else {
    //   SocketClient.getInstance().send("status", { token: api_token });
    // }

    if (socket && socket.isLoggingIn) {
      alert("loggedin");
      SocketClient.getInstance().send("status", { token: api_token, id:socket.user.id });
      window.location = Config.Config.REDIRECT_URI;
    }
  }, []);
  const oauthLoader = () => {
    setLoginattempt(true);
    // dispatch({ type: "SET_STAGE", response: "oauth" });
  };

  return (
    <div class="container">
      {/* <div class="section">
        <h1 class="section-title"> Validate Emails</h1>
      </div> */}
      
        <div class="agileits-top">
          {socket.loggedIn ? (
            <div class="row">
              <div class="col-sm-5 ">
                <Credit />
              </div>
              <div class="col-sm-1"></div>
              <div class="col-sm-6 ">
                <Scan />
              </div>
            </div>
          ) : (
            <div>
              {loginattempt ? (
                <div style={{ display: "flex" }}>
                  <h3 style={{ margin: "auto 20px " }}>
                    {" "}
                    Connecting Pipedrive
                  </h3>
                  <span>
                    <Loader
                      type="ThreeDots"
                      color="#2BAD60"
                      height="80"
                      width="80"
                    />
                  </span>
                </div>
              ) : (
                <div>
                  <a
                    target="blank"
                    // href="https://oauth.pipedrive.com/oauth/authorize?client_id=67c7097db1c334ad&amp;redirect_uri=https://localhost:3000"
                    href="https://oauth.pipedrive.com/oauth/authorize?client_id=67c7097db1c334ad&amp;redirect_uri=https://server.certalink.com"

                  >
                    <button id="loginButton" onClick={oauthLoader}>
                      Connect to Pipedrive
                    </button>
                  </a>
                </div>
              )}
            </div>
          )}
        </div>
      
    </div>
  );
};

export default Home;

